const shopCart = {
  state: {
    goods: [
    {
      id: '0',
      name: '猪肉白菜水饺',    
      price: 16,
      num: 0,
      img:'https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1578297647&di=b5746f7d96262ca45878956d680d46b5&src=http://bpic.588ku.com/element_origin_min_pic/16/11/17/1a12cf9e989b9d05ca6cc11de4ddd221.jpg'
    },
    {
      id: '1',
      name: '鲅鱼三鲜水饺',
      price: 20,
      num: 0,
			img:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1578307756833&di=a9d25189ef1f77e4defb808bcb85ff35&imgtype=0&src=http%3A%2F%2Fp1.s.hjfile.cn%2Fthread%2F201112%2F20111229121322413_501_o.jpg'

    },
    {
      id: '2',
      name: '虾仁三鲜水饺',
      price: 25,
      num: 0,
			img:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1578307803762&di=162c44200663163e9a7d832f099bd5bf&imgtype=jpg&src=http%3A%2F%2Fimg2.imgtn.bdimg.com%2Fit%2Fu%3D2243269147%2C794093626%26fm%3D214%26gp%3D0.jpg'

    }
  ],
    totalPrice: 0.00,
    totalNum: 0
  },
  mutations: {
    ADD_CART (state, id) {
      state.goods[id].num++
      state.totalNum++
      state.totalPrice += state.goods[id].price
    },
    REDUCE_CART (state, id) {
      if (state.goods[id].num > 0) {
        state.goods[id].num--
        state.totalNum--
        state.totalPrice -= state.goods[id].price
      }
    }
  }
}

export default shopCart



